<template>
  <div id="app">
    <TodoHeader/>
    <TodoInput @addTodoItem="addOneItem"/>
    <section>
      <div class="top">
        <h2 class="title">ü©∑ Ïò§ÎäòÏùò Í≥ÑÌöç ü©∑ </h2>
        <TodoSelect :selectedValue="incompleteSortOptions" @selectedValue="setIncompleteSortOptions"/>
      </div>
      <TodoList 
      :propsdata="incompleteItems" 
      @removeItem="removeOneItem"
      @toggleItem="toggleOneItem"/>
      <!-- Ï°∞Í±¥Î∂Ä Î†åÎçîÎßÅÏúºÎ°ú Ìï† ÏùºÏù¥ ÏóÜÎäî Í≤ΩÏö∞ Î¨∏Íµ¨ ÌëúÏãú -->
      <div v-if="!incompleteItems.length" class="empty-message">
        Ïò§ÎäòÏùò Ìï† ÏùºÏùÑ Îì±Î°ùÌï¥Î≥¥ÏÑ∏Ïöî.
      </div>
    </section>

    <section>
      <div class="top">
        <h2 class="title">üíñ ÏôÑÎ£åÌïú Ïùº üíñ</h2>
        <TodoSelect :selectedValue="completedSortOptions" @selectedValue="setCompletedSortOptions"/>
      </div>
      <TodoList 
      :propsdata="completedItems" 
      @removeItem="removeOneItem"
      @toggleItem="toggleOneItem"/>
      <!-- Ï°∞Í±¥Î∂Ä Î†åÎçîÎßÅÏúºÎ°ú ÏôÑÎ£åÎêú ÏùºÏù¥ ÏóÜÎäî Í≤ΩÏö∞ Î¨∏Íµ¨ ÌëúÏãú -->
      <div v-if="!completedItems.length" class="empty-message">
        ÏôÑÎ£åÌïú ÏùºÏùÑ Ï≤¥ÌÅ¨Ìï¥Î≥¥ÏÑ∏Ïöî.
      </div>
    </section>

    <TodoFooter
      @clearAll="clearAllItems"/>
  </div>
</template>

<script>
import TodoHeader from './components/TodoHeader.vue'
import TodoInput from './components/TodoInput.vue'
import TodoSelect from './components/TodoSelect.vue'
import TodoList from './components/TodoList.vue'
import TodoFooter from './components/TodoFooter.vue'

export default {
  data(){
    return{
      todoItems: [],
      incompleteSortOptions: 'latest', // Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï
      completedSortOptions: 'latest', // Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï
    }
  },
  computed: {
    incompleteItems() {
      return this.sortItems(this.todoItems.filter(item => !item.completed), this.incompleteSortOptions);
    },
    completedItems() {
      return this.sortItems(this.todoItems.filter(item => item.completed), this.completedSortOptions);
    }
  },
  methods:{
    getCurrentDateTime() {
      const now = new Date();
      return now.toLocaleString('ko-KR', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        hour12: false // 24ÏãúÍ∞Ñ ÌòïÏãù
      });
    },
    addOneItem(todoItem){
      const obj = {
        id: `TODO_${Date.now().toString()}`,
        contents: todoItem,
        inputDate: this.getCurrentDateTime(),
        // modifyDate: 'ÏàòÏ†ïÎÇ†Ïßú',
        completed: false,
        deleted: false
      };
      localStorage.setItem(obj.id, JSON.stringify(obj));
      this.todoItems = [...this.todoItems, obj]; // Î∞∞Ïó¥ÏùÑ ÏÉà Î∞∞Ïó¥Î°ú ÏóÖÎç∞Ïù¥Ìä∏
      
      // ÏÉà Ìï≠Î™© Ï∂îÍ∞Ä ÌõÑ Ï†ïÎ†¨ Ï†ÅÏö©
      this.sortAllItems(this.incompleteSortOptions, 'incomplete');
      this.sortAllItems(this.completedSortOptions, 'completed');
    },
    removeOneItem(id){
      // 1) idÍ∞íÏúºÎ°ú item Îç∞Ïù¥ÌÑ∞Í∞íÏùÑ Í∞ÄÏ†∏Ïò¥.
      let item = JSON.parse(localStorage.getItem(id));

      // 2) Ìï¥Îãπ idÍ∞íÏù¥ ÏûàÎäî Í≤ΩÏö∞
      if (item) {
        // 2-1) deleted Í∞íÏùÑ trueÎ°ú ÏÑ§Ï†ï
        item.deleted = true;

        // 2-2) ÏàòÏ†ïÎêú ÏïÑÏù¥ÌÖú Î∞∞Ïó¥ÏùÑ Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•
        localStorage.setItem(id, JSON.stringify(item));

        // 2-3) ÏÉÅÌÉúÎ•º ÌïÑÌÑ∞ÎßÅÌïòÏó¨ ÏÉà Î∞∞Ïó¥Î°ú ÏóÖÎç∞Ïù¥Ìä∏
        this.todoItems = this.todoItems.filter(i => i.id !== id && !i.deleted);
      }
  },
    toggleOneItem(id){
      let item = JSON.parse(localStorage.getItem(id));

      // 1) ÏïÑÏù¥ÌÖúÏù¥ Ï°¥Ïû¨ÌïòÎ©¥ ÏôÑÎ£å ÏÉÅÌÉúÎ•º ÌÜ†Í∏Ä
      if (item) {
        item.completed = !item.completed;

        // 2) ÏàòÏ†ïÎêú ÏïÑÏù¥ÌÖúÏùÑ Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•
        localStorage.setItem(id, JSON.stringify(item));

        // 3) Îç∞Ïù¥ÌÑ∞ Î∞∞Ïó¥ÏùÑ ÏóÖÎç∞Ïù¥Ìä∏ÌïòÏó¨ ÌôîÎ©¥Ïóê Î∞òÏòÅ
        this.todoItems = this.todoItems.map(i =>
          i.id === id ? { ...i, completed: item.completed } : i
        );
      }
    },
    clearAllItems(){
    // 1) Î™®Îì† Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄ Ìï≠Î™©Ïùò ÌÇ§Î•º Í∞ÄÏ†∏Ïò§Í∏∞
    const keys = Object.keys(localStorage);

    keys.forEach(key => {
      // 2) Ìï≠Î™©Ïùò Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏ÏôÄÏÑú JSON ÌååÏã±ÌïòÍ∏∞
      let item = JSON.parse(localStorage.getItem(key));
      
      // 3) `deleted` ÏÜçÏÑ±ÏùÑ "true"Î°ú ÏÑ§Ï†ï
      item.deleted = true;
      
      // 4) ÏàòÏ†ïÎêú Îç∞Ïù¥ÌÑ∞Î•º Îã§Ïãú Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•ÌïòÍ∏∞
      localStorage.setItem(key, JSON.stringify(item));

      // 5) ÏÉÅÌÉúÎ•º ÌïÑÌÑ∞ÎßÅÌïòÏó¨ ÏÉà Î∞∞Ïó¥Î°ú ÏóÖÎç∞Ïù¥Ìä∏
      this.todoItems = [];
    });
    },
    setIncompleteSortOptions(selectedValue) {
      this.incompleteSortOptions = selectedValue;
    },
    setCompletedSortOptions(selectedValue) {
      this.completedSortOptions = selectedValue;
    },
    sortItems(items, sortOption) {
      let sorted = [...items];
      switch(sortOption) {
        case 'latest':
          sorted.sort((a, b) => b.id.localeCompare(a.id));
          break;
        case 'oldest':
          sorted.sort((a, b) => a.id.localeCompare(b.id));
          break;
        case 'alphabetical':
          sorted.sort((a, b) => a.contents.localeCompare(b.contents));
          break;
      }
      return sorted;
    },
    sortAllItems(sortOption, type) {
      if (type === 'incomplete') {
        this.incompleteItems = this.sortItems(this.todoItems.filter(item => !item.completed), sortOption);
      } else if (type === 'completed') {
        this.completedItems = this.sortItems(this.todoItems.filter(item => item.completed), sortOption);
      }
    }
  },
  created(){
    if(localStorage.length > 0){
      for(let i=0; i < localStorage.length; i++){
        const key = localStorage.key(i);

        if(key && key !== 'loglevel:webpack-dev-serve'){
          const item = JSON.parse(localStorage.getItem(key));
          if (item && !item.deleted) {
            this.todoItems.push(item);
          }
        }
      }
    }

    // sort Í∏∞Î≥∏Í∞í Ï†ïÏùò Î∞è Ïã§Ìñâ
    this.sortAllItems(this.incompleteSortOptions, 'incomplete');
    this.sortAllItems(this.completedSortOptions, 'completed');
  },
  name: 'App',
  components: {
    TodoHeader,
    TodoInput,
    TodoSelect,
    TodoList,
    TodoFooter,
  }
}
</script>

